* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    text-align: center;
}

.title {
    margin-top: 50px;
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.title span {
    color: #26ffcb;
}

.reset {
    width: 250px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background-color: #1f3540;
    font-size: 26px;
    color: #26ffcb;
}

.reset:hover {
    background-color: #182931;
}

.board {
    height: 600px;
    width: 564px;
    margin: auto;
    display: flex;
    margin-top: 50px;
}

.boxes {
    height: 180px;
    width: 180px;
    background-color: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
    display: flex;
    padding: 40px;
}


